<template>
    <div class="content">

        <el-dialog :title="interface.title" v-model="interface.visible">
           <div class="interface">
                <div class="form">
                    <div>
                        <input type="hidden" name="_method" value="DELETE">
                        Produto: <el-input v-model="interface.name"></el-input>
                    </div>
                    <div>
                        Quantidade: <el-input-number v-model="interface.amount" :min="0"></el-input-number>
                    </div>
                    <div>
                        Preço: <el-input v-model="interface.price"></el-input>
                    </div>
                    <div>
                        Tipo: <el-select v-model="interface.type">
                                <el-option v-for="item in optionsType" :label="item.text" :value="item.val"></el-option>
                            </el-select>
                    </div>
                </div>
                <div class="buttons">
                    <el-button @click="cancel">Cancelar</el-button>
                    <el-button @click="save" type="success">Salvar</el-button>
                </div>
           </div>
        </el-dialog>

        <el-row :class="{'row-bg': true}" type="flex">
            <el-col>
                <el-button type="text" @click="addProduct">Adicionar Produto</el-button>
            </el-col>
            <el-col>
                <el-table class="tabela" :data="tableData" border max-height="400">
                    <el-table-column prop="name" label="Produto" min-width="150" sortable></el-table-column>
                    <el-table-column prop="amount" label="Qtde" width="100" align="right" sortable></el-table-column>
                    <el-table-column prop="price" label="PU" width="80" align="right" sortable></el-table-column>
                    <el-table-column prop="type" label="Tipo" min-width="100" :filter-method="filterTag" :filters="optionsType">
                        <template scope="scope">
                            <div class="tags">
                                <el-tag :type="tipoTag(scope.row.type)" close-transition>{{scope.row.type}}</el-tag>
                            </div>
                        </template>
                    </el-table-column>
                    <el-table-column label="Operações" min-width="105">
                        <template scope="scope">
                            <div class="op">
                                <el-button size="small" @click="editarProd(scope.$index, scope.row)">Editar</el-button>
                                <el-button size="small" type="danger" @click="deletarProd(scope.$index, scope.row)">Apagar</el-button>
                            </div>
                        </template>
                    </el-table-column>
                </el-table>
            </el-col>
        </el-row>
    </div>
</template>
<script>


    import axios from 'axios';
    export default {
        data() {
            return {
                tableData: [],
                produtosLista: [],
                interface: {
                    visible: false,
                    id: '',
                    title: '',
                    name: '',
                    amount: 0,
                    price: '',
                    type: '',
                    edit: false
                },
                optionsType: [{
                    text: 'Lanche',
                    value: 'Lanche',
                    val: '1'
                }, {
                    text: 'Refeição',
                    value: 'Refeição',
                    val: '2',
                }, {
                    text: 'Outros',
                    value: 'Outros',
                    val: '3'
                }]
            }
        },
        mounted() {
            this.request();
        },
        watch: {
            produtosLista(list) {
                //console.log(list);
                this.tableData = [];
                let elems = list.slice();
                for(let n in elems) {
                    switch(elems[n].type) {
                        case 1: elems[n].type = 'Lanche'; break;
                        case 2: elems[n].type = 'Refeição'; break;
                        case 3: elems[n].type = 'Outros'; break;
                    }
                    this.tableData.unshift(elems[n]);
                }
            }
        },
        methods: {
            request() {
                axios.post('api/cantina/produtos/all').then(response => {
                    this.produtosLista = response.data;
                }).catch(error => {
                    console.log('error');
                    console.log(error);
                });
            },
            cancel() {
                this.interface.visible = false;
                this.interface.title = '';
                this.interface.name = '';
                this.interface.amount = 0;
                this.interface.price = '';
                this.interface.type = '';
                this.interface.edit = false;
                this.interface.id = '';
            },

            save() {
                let url, obj;

                let type;
                if (this.interface.type === 'Lanche') 
                    type = 1;
                else if (this.interface.type === 'Refeição')
                    type = 2;
                else 
                    type = 3;

                url = 'api/cantina/produtos'
                
                obj = (({name, amount, price}) => ({name, amount, price, type}))(this.interface);
                if (this.interface.edit) {  
                    obj = (({name, amount, price, id}) => ({name, amount, price, type, id}))(this.interface);
                    url = 'api/cantina/produtos/'+obj.id;
                }
                
                //console.log(obj);
                axios.put(url, obj).then(response => {
                    console.log('response');
                    console.log(response);
                    this.$message({
                        message: response.data.success,
                        type: 'success'
                    });
                    this.interface.visible = false;
                    this.request();
                }).catch(error => {
                    console.log('error');
                    console.log(error);
                });
                //if(this.interface.edit)
            },

            editarProd(index, row) {
                console.log(row);
                this.interface.name = row.name;
                this.interface.amount = row.amount;
                this.interface.price = row.price;
                this.interface.type = row.type;
                this.interface.edit = true;
                this.interface.id = row.id;

                this.interface.title = "Editar Produto";
                this.interface.visible = true;
            },

            deletarProd(index, row) {
                this.$confirm('Deseja mesmo remover o produto?', 'Aviso', {
                    confirmButtonText: 'Sim',
                    cancelButtonText: 'Não',
                    type: 'warning'
                }).then(() => {
                    axios.post('api/cantina/produtos/'+row.id + '/delete').then(response => {
                        console.log('delete');
                        console.log(response);
                        if (response.status === 200) {
                            this.$message({
                                type: 'success',
                                message: 'Produto apagado!'
                            });
                            this.request();
                        }
                    }).catch(error => {
                        console.log('error delete');
                        console.log(error);
                    });
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: 'Produto não apagado'
                    });
                });
            },

            tipoTag(type) {
                if (type === 'Refeição')
                    return 'primary';
                else if (type === 'Lanche')
                    return 'success';
                return 'gray';
            },

            filterTag(value, row) {
                return row.type === value;
            },

            addProduct() {
                this.interface.title = "Adicionar Produto";
                this.interface.visible = true;
            }
        }
    }
</script>
<style scoped>
    .content {
        height: 100vh;
        display: flex;
        justify-content: center;
    }
    .row-bg {
        flex-direction: column;
        margin-top: 3vh;
        align-items: center;
    }
    .tabela {
        width: 100%;
    }

    .op {
        display: flex;
        flex-direction: column;
        justify-content: center;
    }
    .op > * {
        margin-left: 0 !important;
        margin-top: 5px;
        margin-bottom: 5px;
    }

    .tags {
        display: flex;
        justify-content: center;
    }
    .interface {
        display: flex;
        flex-direction: column;
    }

    .buttons {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
    }

    .form, .form > * {
        display: flex;
        flex-direction: column;
        margin-bottom: 3px;
    }

</style>


